import 'core-js/es6/promise'
import 'core-js/es6/string'
import 'core-js/es7/array'
// import cssVars from 'css-vars-ponyfill'
import Vue from 'vue'
import BootstrapVue from 'bootstrap-vue'
import App from './App'
import router from './router'
import firebase from 'firebase'

// todo
// cssVars()

Vue.use(BootstrapVue)

 // Initialize Firebase
 let app;
 var config = {
  apiKey: "AIzaSyAche6wzHPDpojO-mhBwmXW07mAijbIwo4",
  authDomain: "coreui-dashboard.firebaseapp.com",
  databaseURL: "https://coreui-dashboard.firebaseio.com",
  projectId: "coreui-dashboard",
  storageBucket: "coreui-dashboard.appspot.com",
  messagingSenderId: "728863032671"
};

firebase.initializeApp(config);
firebase.auth().onAuthStateChanged(function() {
  if (!app) {
    /* eslint-disable no-new */
    new Vue({
      el: '#app',
      router,
      template: '<App/>',
      components: {
        App
      }
    })
  }
})
